<template>
    <el-container>
        <el-header>
            <el-input type="text" v-model="serchkey" placeholder="请输入查询关键字"
                      style="min-width: 200px;max-width: 400px;" clearable prefix-icon="el-icon-search"></el-input>
            <el-button type="primary" @click="reload()" icon="el-icon-search">查询</el-button>
            <el-button type="info" @click="add()" icon="el-icon-news">新增</el-button>
        </el-header>
        <el-main>
            <el-row>
                <el-col>
                    <el-table
                            :data="tableData"
                            border
                            stripe
                            style="width: 100%;">

                        <!--<el-table-column
                                type="selection"
                                width="55">
                        </el-table-column>-->

                        <el-table-column
                                label="缩略图">
                            <template slot-scope="scope">
                                <img :src="scope.row.cover" style="max-height: 100px">
                            </template>
                        </el-table-column>

                        <el-table-column
                                prop="name"
                                label="名称">
                        </el-table-column>

                        <el-table-column
                                prop="stock"
                                label="库存">
                        </el-table-column>

                        <el-table-column
                                prop="price"
                                label="价格">
                        </el-table-column>


                        <el-table-column
                                label="上架状态">
                            <template slot-scope="scope">
                                <el-switch
                                        :value="scope.row.state+''" @change=change(scope.row)
                                        active-value="0"
                                        inactive-value="1">
                                </el-switch>
                            </template>
                        </el-table-column>

                        <el-table-column
                                label="操作"
                                width="185">
                            <template slot-scope="scope">
                                <el-button @click="editHandleClick(scope.row.id)" size="mini" type="primary"
                                           icon="el-icon-edit">
                                    编辑
                                </el-button>
                                <el-button @click="deleteHandleClick(scope.row.id)" type="danger" size="mini"
                                           icon="el-icon-delete">
                                    删除
                                </el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-col>
            </el-row>
            <el-row>
                <el-pagination
                        @current-change="handleCurrentChange"
                        :current-page.sync="currentPage"
                        :page-size="pageSize"
                        layout="total, prev, pager, next"
                        :total="countSize">
                </el-pagination>
            </el-row>
        </el-main>
    </el-container>
</template>

<script>
    export default {
        data() {
            return {
                serchkey: '',
                tableData: null,
                currentPage: 1,
                countSize: 0,
                pageSize: 1
            }
        },
        mounted() {
            this.reload();
        },
        methods: {
            change(row) {
                console.log(row)
                row.state = row.state == 0 ? 1 : 0;
                var tip = '上架';
                if (row.state == 1) {
                    tip = '下架';
                }
                this.axios.post('/api/product/state/' + row.id + "/" + row.state).then((response) => {
                    if (response.data.state == '0') {
                        this.$message({
                            type: 'success',
                            message: tip + '成功'
                        });
                    } else {
                        this.$message({
                            type: 'error',
                            message: tip + '失败'
                        });
                    }
                }).catch((response) => {
                    console.log(response);
                })
            },
            add() {
                this.$router.push({path: '/productManage/add', replace: true});
            },
            editHandleClick(id) {
                this.$router.push({path: '/productManage/add', query: {id: id}, replace: true});
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.reload();
            },
            reload() {
                this.axios.get("/api/product/page?name=" + this.serchkey + "&page=" + this.currentPage).then((response) => {
                    this.tableData = response.data.data.content;
                    this.currentPage = response.data.data.number + 1;
                    this.countSize = response.data.data.totalElements;
                    this.pageSize = response.data.data.size;
                }).catch((response) => {
                    console.log(response);
                })
            },
            deleteHandleClick(id) {
                this.$confirm('此操作将永久删除, 是否继续?', '提示', {
                    confirmButtonText: '确认',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.axios.post('/api/product/delete/' + id).then((response) => {
                        if (response.data.state == '0') {
                            //刷新数据
                            this.reload();
                        } else {
                            console.log(response);
                            this.$message({
                                type: 'error',
                                message: '删除失败'
                            });
                        }
                    }).catch((response) => {
                        console.log(response);
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            }
        }
    }
</script>

<style scoped>
    .el-input {
        margin-right: 10px;
    }

</style>
